/* Body and background */
body {
    margin: 0;
    padding: 0;
    font-family: Arial, sans-serif;
    background-color: #333;
    color: #fff;
}

/* Centering the login container */
.login-page {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background-color: #333;
}

/* Main content for login */
.login-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
}

/* Direct to Dashboard link */
.direct-dashboard-link {
    margin-bottom: 20px;
}

.direct-dashboard-link a {
    color: #00FF00;
    font-size: 18px;
}

.direct-dashboard-link a:hover {
    text-decoration: underline;
}

/* Login Box */
.login-box {
    background-color: #444;
    padding: 40px;
    border-radius: 8px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
    width: 100%;
    max-width: 400px;
    text-align: center;
}

/* Heading for Login */
h2 {
    font-size: 24px;
    color: #fff;
    margin-bottom: 20px;
}

/* Label */
.login-box label {
    color: #fff;
    font-size: 16px;
    margin-top: 10px;
}

/* Input Fields */
.login-box input {
    width: 100%;
    padding: 12px;
    margin: 10px 0;
    border-radius: 4px;
    border: 1px solid #ccc;
    background-color: #555;
    color: #fff;
    font-size: 16px;
}

/* Buttons */
.login-box button {
    width: 100%;
    padding: 12px;
    background-color: #00FF00;
    border: none;
    border-radius: 4px;
    color: #333;
    font-size: 16px;
    cursor: pointer;
}

.login-box button:hover {
    background-color: #00cc00;
}

/* Form Links */
.login-box p {
    margin-top: 10px;
}

.login-box p a {
    color: #00FF00;
}

.login-box p a:hover {
    text-decoration: underline;
}

/* Signup Link */
#signup-link {
    color: #00FF00;
    cursor: pointer;
    font-size: 16px;
}

/* Sidebar and main content for the dashboard and product pages */
.sidebar {
    background-color: #222;
    color: white;
    width: 250px;
    padding: 20px;
    position: fixed;
    height: 100%;
    top: 0;
    left: 0;
    display: flex;
    flex-direction: column;
}

.sidebar h2 {
    color: #4CAF50;
    font-size: 1.8rem;
    margin-bottom: 20px;
    font-weight: bold;
}

.sidebar ul {
    list-style-type: none;
    padding: 0;
}

.sidebar li {
    margin: 15px 0;
}

.sidebar a {
    color: white;
    text-decoration: none;
    font-size: 1.1rem;
}

.sidebar a:hover {
    color: #4CAF50;
    font-weight: bold;
}

/* Main content area */
.main-content {
    margin-left: 250px;
    padding: 40px;
    height: 100vh;
    background-color: #2b2b2b;
}

/* Dashboard header */
header {
    background-color: #333;
    padding: 20px;
    text-align: center;
    color: white;
    font-size: 2rem;
}

/* Add Product Page */
.add-product {
    margin-top: 40px;
}

.add-product form {
    display: flex;
    flex-direction: column;
    max-width: 500px;
    margin: 0 auto;
}

.add-product input,
.add-product textarea {
    padding: 10px;
    margin: 10px 0;
    border: 1px solid #444;
    border-radius: 4px;
    background-color: #333;
    color: white;
}

.add-product button {
    padding: 12px;
    background-color: #4CAF50;
    color: white;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    font-size: 1rem;
}

.add-product button:hover {
    background-color: #45a049;
}

/* Tables & other content */
table {
    width: 100%;
    margin-top: 20px;
    border-collapse: collapse;
}

th, td {
    padding: 12px;
    text-align: left;
    border-bottom: 1px solid #444;
}

th {
    background-color: #333;
    color: white;
}

td {
    background-color: #222;
}

/* Make the sidebar and content responsive */
@media (max-width: 768px) {
    .sidebar {
        width: 200px;
    }

    .main-content {
        margin-left: 200px;
    }

    .auth-container {
        height: auto;
        padding: 20px;
    }
}
